body{
    background-color: #d7d5d5;
    font-family: Monda;
}

.sign-in-sign-up {
    display: flex;
    flex-direction: column;
    align-items: center;
    
    

   
      
}
.sign-in-sign-up h2{
    font-weight: bold;
    color: #1A120B;

    /* font-size: 1rem; */
}


form {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2px;
 
    border-radius: 25px;
    background-color:#fff;
    margin-top: 130px;

    /* border-top: 1px solid black;
    border-left: 1px solid black; */
    
}


input[type="email"]
 {
    margin-bottom: 20px;
    padding: 5px  10px 5px 40px;
    font-size: 16px;
    border: 1px solid #3C2A21;
    border-radius: 5px;
    background-color: white;
    color: #3C2A21;
    font-weight: bold;
    width: 250px;
    height: 35px;

    
}
.input-password{
    margin-bottom: 20px;
    padding: 5px  10px 5px 40px;
    font-size: 16px;
    border: 1px solid #3C2A21;
    border-radius: 5px;
    background-color: white;
    color: #3C2A21;
    font-weight: bold;
    width: 250px;
    height: 35px;
    margin-left: 20px;
}
.icon{
    position: absolute;
    align-items: center;
    justify-content: center;
    display: flex;
    padding-left: 10px;
    padding-top: 15px;
}
.iconn{
padding-left: 30px;
}
.icons{
   position: relative;
    right: 30px;
}
.icons:hover{
    color: rgb(66, 66, 49);
}

button[type="submit"] {
    padding: 10px 20px;
    
    border-radius: 5px;
    cursor: pointer;
    transition: 0.4s;
     background-color: #1A120B ;
   
    font-weight: bold;
    font-size: 1rem;
    width: 270px;
    border-radius: 100px;
    color: #E5E5CB;
}

button[type="submit"]:hover {
   background-color: #E5E5CB;
   color:#1A120B ;
    /* width: 100px; */
  
}

p {
    margin-top: 20px;
    font-size: 16px;
    color: #1A120B;
    /* font-size: 1.4rem; */
}

p span {
    font-weight: bold;
    color: #1A120B;
    left: 0;
    margin-left: 4px;
    /* font-size: 1rem; */
}

